
    import initServer from 'next-plugin-loader?middleware=on-init-server!'
    import onError from 'next-plugin-loader?middleware=on-error-server!'
    
    const runtimeConfig = {}
    const {parse} = require('url')
    const {parse: parseQs} = require('querystring')
    const {renderToHTML} =require('next/dist/next-server/server/render');
    const {sendHTML} = require('next/dist/next-server/server/send-html');
    const buildManifest = require('private-dot-next/build-manifest.json');
    const reactLoadableManifest = require('private-dot-next/react-loadable-manifest.json');
    const Document = require('private-next-pages/_document.js').default;
    const Error = require('private-next-pages/_error.js').default;
    const App = require('private-next-pages/_app.js').default;
    
    const { getRouteMatcher } = require('next/dist/next-server/lib/router/utils/route-matcher');
      const { getRouteRegex } = require('next/dist/next-server/lib/router/utils/route-regex');
  
    
    const { rewrites } = require('private-dot-next/routes-manifest.json')
    const { pathToRegexp, default: pathMatch } = require('next/dist/next-server/server/lib/path-match')
  

    const ComponentInfo = require('private-next-pages/[company]/settings/roles/index.js')

    const Component = ComponentInfo.default
    export default Component
    export const unstable_getStaticProps = ComponentInfo['unstable_getStaticProp' + 's']
    export const unstable_getStaticParams = ComponentInfo['unstable_getStaticParam' + 's']
    export const unstable_getStaticPaths = ComponentInfo['unstable_getStaticPath' + 's']
    export const unstable_getServerProps = ComponentInfo['unstable_getServerProp' + 's']

    
    const dynamicRouteMatcher = getRouteMatcher(getRouteRegex("/[company]/settings/roles"))
  
    
    const getCustomRouteMatcher = pathMatch(true)

    function handleRewrites(parsedUrl) {
      for (const rewrite of rewrites) {
        const matcher = getCustomRouteMatcher(rewrite.source)
        const params = matcher(parsedUrl.pathname)

        if (params) {
          parsedUrl.query = {
            ...parsedUrl.query,
            ...params
          }
          const parsedDest = parse(rewrite.destination)
          const destCompiler = pathToRegexp.compile(
            `${parsedDest.pathname}${parsedDest.hash || ''}`
          )
          const newUrl = destCompiler(params)
          const parsedNewUrl = parse(newUrl)

          parsedUrl.pathname = parsedNewUrl.pathname
          parsedUrl.hash = parsedNewUrl.hash

          if (parsedUrl.pathname === '/[company]/settings/roles'){
            break
          }
          
            const dynamicParams = dynamicRouteMatcher(parsedUrl.pathname);            if (dynamicParams) {
              parsedUrl.query = {
                ...parsedUrl.query,
                ...dynamicParams
              }
              break
            }
          
        }
      }

      return parsedUrl
    }
  

    export const config = ComponentInfo['confi' + 'g'] || {}
    export const _app = App
    export async function renderReqToHTML(req, res, fromExport, _renderOpts, _params) {
      
      const options = {
        App,
        Document,
        buildManifest,
        unstable_getStaticProps,
        unstable_getServerProps,
        unstable_getStaticPaths,
        reactLoadableManifest,
        canonicalBase: "",
        buildId: "sR8E943MeF8iizUTiMw93",
        assetPrefix: "",
        runtimeConfig: runtimeConfig.publicRuntimeConfig || {},
        previewProps: {previewModeId:"609caa2c08a401c5bf32f3fc25af9097",previewModeSigningKey:"0da947cb1a9ab1a2963270563d98b07ecb5bf49046618b8cf90b3d074a7f8610",previewModeEncryptionKey:"da2b641ab67e6721fc50a478a49cbc3b4900768642bbad9e97222d934aa6a207"},
        ..._renderOpts
      }
      let _nextData = false

      const parsedUrl = handleRewrites(parse(req.url, true))

      if (parsedUrl.pathname.match(/_next\/data/)) {
        _nextData = true
        parsedUrl.pathname = parsedUrl.pathname
          .replace(new RegExp('/_next/data/sR8E943MeF8iizUTiMw93/'), '/')
          .replace(/\.json$/, '')
      }

      const renderOpts = Object.assign(
        {
          Component,
          pageConfig: config,
          nextExport: fromExport
        },
        options,
      )
      try {
        
        const params = fromExport && !unstable_getStaticProps && !unstable_getServerProps ? {} : dynamicRouteMatcher(parsedUrl.pathname) || {};
        const nowParams = req.headers && req.headers["x-now-route-matches"]
              ? getRouteMatcher(
                  (function() {
                    const { re, groups } = getRouteRegex("/[company]/settings/roles");
                    return {
                      re: {
                        // Simulate a RegExp match from the `req.url` input
                        exec: str => {
                          const obj = parseQs(str);
                          return Object.keys(obj).reduce(
                            (prev, key) =>
                              Object.assign(prev, {
                                [key]: encodeURIComponent(obj[key])
                              }),
                            {}
                          );
                        }
                      },
                      groups
                    };
                  })()
                )(req.headers["x-now-route-matches"])
              : null;
          
        // make sure to set renderOpts to the correct params e.g. _params
        // if provided from worker or params if we're parsing them here
        renderOpts.params = _params || params

        const isFallback = parsedUrl.query.__nextFallback

        let result = await renderToHTML(req, res, "/[company]/settings/roles", Object.assign({}, unstable_getStaticProps ? {} : parsedUrl.query, nowParams ? nowParams : params, _params, isFallback ? { __nextFallback: 'true' } : {}), renderOpts)

        if (_nextData && !fromExport) {
          const payload = JSON.stringify(renderOpts.pageData)
          res.setHeader('Content-Type', 'application/json')
          res.setHeader('Content-Length', Buffer.byteLength(payload))

          res.setHeader(
            'Cache-Control',
            unstable_getServerProps
              ? `no-cache, no-store, must-revalidate`
              : `s-maxage=${renderOpts.revalidate}, stale-while-revalidate`
          )
          res.end(payload)
          return null
        }

        if (fromExport) return { html: result, renderOpts }
        return result
      } catch (err) {
        if (err.code === 'ENOENT') {
          res.statusCode = 404
          const result = await renderToHTML(req, res, "/_error", parsedUrl.query, Object.assign({}, options, {
            unstable_getStaticProps: undefined,
            unstable_getStaticPaths: undefined,
            unstable_getServerProps: undefined,
            Component: Error
          }))
          return result
        } else {
          console.error(err)
          res.statusCode = 500
          const result = await renderToHTML(req, res, "/_error", parsedUrl.query, Object.assign({}, options, {
            unstable_getStaticProps: undefined,
            unstable_getStaticPaths: undefined,
            unstable_getServerProps: undefined,
            Component: Error,
            err
          }))
          return result
        }
      }
    }
    export async function render (req, res) {
      try {
        await initServer()
        const html = await renderReqToHTML(req, res)
        if (html) {
          sendHTML(req, res, html, {generateEtags: true})
        }
      } catch(err) {
        await onError(err)
        console.error(err)
        res.statusCode = 500
        res.end('Internal Server Error')
      }
    }
  